import React, { Component } from 'react'

import Header from '../components/header'
import Section from '../components/section'
import ProjectBoard from '../components/project-board'
import Vimeo from '../components/vimeo'
import Modal from '../components/modal'

class Homepage extends Component {

    state = { }
    
    componentDidMount() {
        document.title = "BillieJH"
    }

    openModal = content => {
        console.log(content)
        this.setState({ modalActive: true, modalContent: content })
    }
    closeModal = props => {
        if(props.target.className === "modal-wrapper")
            this.setState({ modalActive: false })
    }

    render() {
        return (
            <div>
                <div style={this.state.modalActive ? { filter: "blur(10px)" } : {} }>
                    <Header />
                    <Section title="Showreels">
                        <Vimeo link={`https://player.vimeo.com/video/334011585?h=d0320a818e&color=D96690&portrait=0`} />
                        <Vimeo link={`https://player.vimeo.com/video/372137247?h=37d9033a62&color=D96690&portrait=0`} />
                    </Section>
                    <Section title="Projects">
                        <ProjectBoard openModal={ this.openModal }/>
                    </Section>
                </div>
                { this.state.modalActive && 
                    <Modal close={ this.closeModal }>
                        { this.state.modalContent }
                    </Modal> }
            </div>
        )
    }
}

export default Homepage;